<template>
  <div id="news">
    <h1>最新消息</h1>
    <div class="news__container">
      <ul>
        <li v-for="(item, index) in news_list"
        :key="index">
          <div class="img">
            <a href="#"
            @click="go_page(`front/news_detail/${item.id}`)">
              <img :src="item.banner" alt="">
            </a>
          </div>
          <div class="main">
            <div class="main__date">{{item.date}}</div>
            <div class="main__title">
              <a href="#"
              @click="go_page(`front/news_detail/${item.id}`)"
              >{{item.title}}</a>
            </div>
            <div class="main__description" v-html="item.description">
            </div>
            <div class="main__link">
              <a href="#"
              @click="go_page(`front/news_detail/${item.id}`)">READ MORE</a>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['news'],
  // {
  //   id: '',
  //   title: '',
  //   date: '',
  //   description: '',
  //   banner: '',
  //   content: '',
  //   remark: '',
  // },
  data() {
    return {
      news_list: [
      ],
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
  },
  created() {
    this.news_list = this.news
      .map((item) => {
        const newObj = {};
        newObj.id = item.id;
        newObj.title = item.title;
        newObj.date = item.date;
        newObj.description = item.description;
        newObj.banner = item.banner;
        return newObj;
      });
  },
};
</script>
